.btn {
  justify-content: center;
  width: 320px;
  height: 56px;
  background-color: #1b31a8;
  color: white;
  align-items: center;
  font-size: 16px;
  font-weight: bold;
  border-radius: 32px;
  margin-top: 32px;
  margin-left: auto;
  margin-right: auto;
}

.btn:hover {
  cursor: pointer;
}

@media screen and (max-width: 560px) {
  .btn {
    width: 100%;
  }
}
