.content__container {
  display: flex;
  flex-direction: column;
  margin-left: auto;
  margin-right: auto;
  width: 480px;
  border-radius: 8px;
  box-shadow: 0px 16px 32px rgba(30, 42, 50, 0.08);
  padding: 35px 40px 40px;
}

@media screen and (max-width: 480px) {
  .content__container {
    width: 280px;
    margin: 0px;
  }
}
